import {Component, Input, OnInit} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { RequestFormService } from "../request-form.service";

@Component({
  selector: 'app-net-params-form',
  templateUrl: './net-params-form.component.html',
  styleUrls: ['./net-params-form.component.css'],
  providers: [RequestFormService]
})
export class NetParamsFormComponent implements OnInit {
  public form: FormGroup;
  get getPotential(){
    return this.form.value.potential;
  };

  @Input() public isVisibleFields = false;

  public getVoltage(){
    debugger;
    return this.maxPotential;
  }

  public maxWorkPotential;
  public maxPotential:string='';
  public shockAmperage = '';
  public kzAmperageImpulse = ''; 
  public ratedVoltageData = [
    {
      value: 0.66,
      maxValue: 0.72
    },
    {
      value:1,
      maxValue: 1.1
    },
    {
      value: 3,
      maxValue: 3.6
    },
    {
      value: 6,
      maxValue: 7.2
    },
    {
      value: 10,
      maxValue: 12
    },
    {
      value: 15,
      maxValue: 17.5
    },
    {
      value: 20,
      maxValue: 24
    },
    {
      value: 24,
      maxValue: 26.5
    },
    {
      value: 27,
      maxValue: 30
    },
    {
      value: 35,
      maxValue: 40.5
    },
    {
      value: 110,
      maxValue: 126
    },
    {
      value: 150,
      maxValue: 172
    },
    {
      value: 220,
      maxValue: 252
    },
    {
      value: 330,
      maxValue: 363
    },
    {
      value: 500,
      maxValue: 525
    },
    {
      value: 750,
      maxValue: 787
    }
  ];
  public minAmperage: string = '';
  public maxAmperage: string='';
  constructor(
    protected fb: FormBuilder,
    public requestService: RequestFormService
  ) {
    this.form = fb.group({
      potential: ['', [Validators.required, Validators.min(0)]],
      workPotential: ['', [Validators.required, Validators.min(0)]],
      minPower: [''],
      maxPower: [''],
      minAmperage: [''],
      maxAmperage: [''],
      kzAmperage: [''],
      heatImpulseTime: [''],
      crashModeAmperage: ['']
    });
  }

  public calculateMinAmperage(): void {

    if (this.form.valid) {
      this.minAmperage = "" + (this.form.value.minPower * 1000 / (Math.sqrt(3) * this.form.value.potential)).toFixed(2);
      sessionStorage.setItem("netMinAmperage", this.minAmperage);
    }
  }

  public calculateMaxAmperage(): void {
    if (this.form.valid) {
      this.maxAmperage = "" +  (this.form.value.maxPower * 1000 / (Math.sqrt(3) * this.form.value.potential)).toFixed(2);
      sessionStorage.setItem("netMaxAmperage", this.maxAmperage);
    }
  }

  public checkPotential():void{
    var flag = false;
    if(this.form.value.workPotential > this.maxWorkPotential){
      flag = true;
    }
    if(flag){
      alert('Превышает максимальное рабочее напряжение');
    }
  }

  public changeMaxPotential(value:any):void{
    this.ratedVoltageData.map(config => {
      if(config.value === value){
        this.maxWorkPotential = config.maxValue;
        this.maxPotential = ""+config.value;

        sessionStorage.setItem("maxPotential", this.maxPotential);
      }
    });
    sessionStorage.setItem("potential", value);
  }

  public onKZEntered(value){
    this.shockAmperage = (this.form.value.kzAmperage * 1.8 * Math.sqrt(2)).toFixed(2);
    sessionStorage.setItem("shockAmperage", this.shockAmperage);
  }

  public onHeatImpulseTimeEntered(value){
    this.kzAmperageImpulse = (this.form.value.kzAmperage * this.form.value.kzAmperage * this.form.value.heatImpulseTime).toFixed(2);
    sessionStorage.setItem("kzAmperageImpulse", this.kzAmperageImpulse);
    
  }

  public saveCrashAmperage(value){
    sessionStorage.setItem("crashModeAmperage", value);
  }

  ngOnInit() {
  }
}
