import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormBuilder, FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LayoutModule } from './layout/layout.module';

import { NgSelectModule } from '@ng-select/ng-select';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NetParamsFormComponent } from './features/net-params-form/net-params-form.component';
import { AmperageTransformerComponent } from './features/amperage-transformer/amperage-transformer.component';
import { VoltageTransformerFormComponent } from './features/voltage-transformer-form/voltage-transformer-form.component';

@NgModule({
  declarations: [
    AppComponent,
    NetParamsFormComponent,
    AmperageTransformerComponent,
    VoltageTransformerFormComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ReactiveFormsModule,
    LayoutModule,
    NgSelectModule,
    FormsModule,
    NgbModule.forRoot()
  ],
  providers: [FormBuilder],
  bootstrap: [AppComponent]
})
export class AppModule { }
